.toplevel_page_ditty {
  background: #fff;
  overflow: hidden;
}
.toplevel_page_ditty #wpcontent {
  display: flex;
  flex-direction: row;
  padding-left: 0;
}
.toplevel_page_ditty #wpbody {
  flex: 1;
}
.toplevel_page_ditty #wpbody-content {
  padding: 0;
  float: none;
}

#ditty-editor {
  height: calc(100% - 100px);
  flex: 1;
  display: flex;
  flex-wrap: nowrap;
  box-sizing: border-box;
}
#ditty-editor__wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100vh;
  overflow: hidden;
}
.wp-admin #ditty-editor__wrapper {
  height: calc(100vh - 32px);
}
#ditty-editor * {
  box-sizing: border-box;
}
#ditty-editor__preview {
  flex: 1;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
#ditty-editor__preview body {
  margin: 0;
}
#ditty-editor__editor {
  position: relative;
  width: 350px;
  max-width: 50%;
  height: 100%;
  background: #fff;
  border-left: 1px solid #ccc;
  box-sizing: border-box;
}
#ditty-editor__editor__sizer {
  position: absolute;
  top: 0;
  left: -5px;
  z-index: 9;
  width: 5px;
  height: 100%;
  background: rgba(170, 170, 170, 0.2);
  cursor: ew-resize;
  transition: background-color 0.25s ease;
}
#ditty-editor__editor__sizer:hover {
  background-color: #aaa;
}
#ditty-editor__editor * {
  box-sizing: border-box;
}

@media screen and (max-width: 782px) {
  #ditty-editor {
    flex-direction: column;
  }
  .wp-admin #ditty-editor__wrapper {
    height: calc(100vh - 46px);
  }
  #ditty-editor__editor {
    width: 100% !important;
    max-width: 100% !important;
    height: 350px;
  }
  #ditty-editor__editor__sizer {
    top: -5px;
    left: 0;
    height: 5px;
    width: 100%;
  }
}
@media screen and (min-width: 782px) {
  .wp-admin #ditty-editor__wrapper {
    height: calc(100vh - 46px);
  }
  #ditty-editor__editor {
    height: 100%;
  }
}
